@import "./fonts.scss";

#root {
  background-color: black;
}

body {
  background-color: black;
  height: 100vh;
}

.column {
  p {
    font-family: $body-font;
    color: white;
    font-size: 1.25rem;
    margin: 1rem;
  }
}

.section {
  h1, h2, h3 {
    font-family: $heading-font;
    text-transform: uppercase;
    font-size: 3rem;
    color: white;
  }
}

footer {
  background-color: black;
  width: 100%;
 
  .footer-content {
    margin: 0 auto;
    width: 90%;
    display: flex;
    justify-content: center;
  }
}
